<template>
  <view class="query-daily-report-container">
    <u-form :model="form" ref="uForm">
      <u-form-item label="开始日期">
        <u-datetime-picker v-model="form.startDate" mode="date" :max-date="new Date().getTime()"></u-datetime-picker>
      </u-form-item>
      <u-form-item label="结束日期">
        <u-datetime-picker v-model="form.endDate" mode="date" :max-date="new Date().getTime()"></u-datetime-picker>
      </u-form-item>
    </u-form>
    <u-button @click="handleSearch" type="primary">查询</u-button>

    <view class="daily-report-list" v-if="dailyReports.length > 0">
      <view class="daily-report-item" v-for="(report, index) in dailyReports" :key="index">
        <text>日期：{{ formatDate(report.date) }}</text>
        <text>今日工作内容：{{ report.workContent }}</text>
        <text>明日工作计划：{{ report.tomorrowPlan }}</text>
        <text v-if="report.problems">遇到的问题：{{ report.problems }}</text>
      </view>
    </view>
    <view v-else>暂无数据</view>

    <u-loadmore :status="loadMoreStatus" @loadmore="loadMore" />
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        startDate: new Date(new Date().setDate(new Date().getDate() - 30)).getTime(), // 默认查询最近30天
        endDate: new Date().getTime()
      },
      dailyReports: [],
      page: 1,
      pageSize: 20,
      loadMoreStatus: 'loadmore',
      userInfo: null
    }
  },
  onLoad() {
    this.userInfo = uni.getStorageSync('userInfo')
    if (!this.userInfo) {
      uni.redirectTo({ url: '/pages/login/login' })
      return
    }
    this.handleSearch()
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp)
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`
    },
    handleSearch() {
      this.page = 1
      this.dailyReports = []
      this.loadMoreStatus = 'loadmore'
      this.fetchDailyReports()
    },
    fetchDailyReports() {
      uniCloud.callFunction({
        name: 'queryDailyReports',
        data: {
          userId: this.userInfo._id,
          startDate: this.form.startDate,
          endDate: this.form.endDate,
          page: this.page,
          pageSize: this.pageSize
        }
      }).then(res => {
        if (res.result.code === 0) {
          this.dailyReports = [...this.dailyReports, ...res.result.data]
          if (res.result.data.length < this.pageSize) {
            this.loadMoreStatus = 'nomore'
          } else {
            this.loadMoreStatus = 'loadmore'
          }
        } else {
          uni.showToast({
            title: res.result.message,
            icon: 'none'
          })
        }
      }).catch(err => {
        uni.showToast({
          title: '查询失败，请重试',
          icon: 'none'
        })
      })
    },
    loadMore() {
      if (this.loadMoreStatus === 'loadmore') {
        this.page++
        this.fetchDailyReports()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.query-daily-report-container {
  padding: 20px;
}
.daily-report-list {
  margin-top: 20px;
}
.daily-report-item {
  background-color: #f8f8f8;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}
</style>